import React, { useState, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/core';
import { CardContent, Paper, CardHeader, Button } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import axios from "axios";
import mapboxgl from 'mapbox-gl';
mapboxgl.accessToken = 'pk.eyJ1IjoiaG9hbmdtaW5obmciLCJhIjoiY2s5M25xYTMwMDRhZDNpcDNhOHN1cDRnciJ9.NvYOhaROmMb04qeJyIbG-A';
const mapQuestKey = "G8mrt0NNARb0MjvGEGstp8ACI5GxqdLc" 

const useStyles = makeStyles(() => ({
  reqres: {
    display: 'flex',
    marginTop: '20px',
    padding: 10,
    backgroundColor: '#F65058FF',
  },
  subheader: {
    color: 'white',
    fontWeight: 'bold',
    fontFamily: 'Lexend Giga',
  },
  title: {
    color: 'white',
    fontWeight: 'bold',
    fontFamily: 'Lexend Giga',
  },
}));
const fetchNearbyStoreDataLocation = async (location) => {
  // const response = await fetch(https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=21.025417,105.835258&radius=1500&type=restaurant&key=AIzaSyA8yp4aWovCABcBT6o6H21ZcBAwQqz3XdI)
  const response = await fetch(`https://nominatim.openstreetmap.org/search?q=${location}&format=json&polygon_geojson=1&addressdetails=1`)
  const resJson = await response.json()
  return resJson
}

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const CustomizedTables = ({datas}) =>{
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Shops' Names</StyledTableCell>
            <StyledTableCell align="center">Shops' Types</StyledTableCell>
            <StyledTableCell align="center">Shops' Class</StyledTableCell>
            <StyledTableCell align="center">Shops' Coordinates</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {datas.map((data) => (
            <StyledTableRow key={data.properties.name}>
              <StyledTableCell component="th" scope="row">
                {data.properties.name}
              </StyledTableCell>
              {/* <StyledTableCell align="right">{data.properties.name}</StyledTableCell> */}
              <StyledTableCell align="center">{data.properties.type}</StyledTableCell>
              <StyledTableCell align="center">{data.properties.class}</StyledTableCell>
              <StyledTableCell align="right">{data.geometry.coordinates}</StyledTableCell>

            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

const getLocationFromCoordinates = async (locationCordinate) =>{
  const response = await fetch(`http://open.mapquestapi.com/geocoding/v1/reverse?key=${mapQuestKey}&location=${locationCordinate.lat},${locationCordinate.lon}&includeRoadMetadata=true&includeNearestIntersection=true`)
  const resJson = await response.json()
  return resJson
}

const fetchNearbyStoreData = async (locationCordinate) => {
  var tileset = 'mapbox.mapbox-streets-v8'
  var radius = 2000; //meters
  var limit = 30; // The maximum amount of results to return
  var layers = 'poi_label'
  var query = 'https://api.mapbox.com/v4/' + tileset + '/tilequery/' + locationCordinate.lon + ',' + locationCordinate.lat + '.json?radius=' + radius + '&limit=' + limit + '&layers=' + layers + '&access_token=' + mapboxgl.accessToken

  const response = await fetch(query)
  const resJson = await response.json()
  return resJson
}

const fetchData = async () => {
  const response = await fetch("http://localhost:5000/helpRequest")
  const resJson = await response.json()
  return resJson
}

const RequestResponse = () => {
  const classes = useStyles();
  const { id } = useParams();
  const history = useHistory();
  const [requestData, setRequestData] = useState([])
  const [invalidate, setInvalidate] = useState(true)
  const [thisRequest, setThisRequest] = useState({})

  const [invalidate2, setInvalidate2] = useState(true)
  const [locationCordinate, setLocationCordinate] = useState({
    lat: 0,
    lon: 0
  })

  const [address, setAddress] = useState([])

  const [nearByStore, setNearByStore] = useState([])

  const handlerOfferHelp = () => {
    history.push(`/offer_help/${thisRequest._id}`)
    // console.log(location)
  }

  const loadStore = () => {
    const newQuery = thisRequest.location.split(" ").join("%20")
    fetchNearbyStoreDataLocation(newQuery)
      .then((res) => {
        setLocationCordinate({
          lat: res[0].lat,
          lon: res[0].lon
        })
        setInvalidate2(false)
      })
      .catch((err) => {
        console.log(err)
      })
    
  }

  useEffect(() => {
    if (invalidate) {
      fetchData()
        .then((res) => {
          // console.log(res)
          setRequestData(res)
          const currentRequest = res.find((req) => req._id === id)
          setThisRequest(currentRequest)
          setInvalidate(false)
        })
        .catch((err) => {
          console.log(err)
        })
    }
  }, [invalidate]);


  useEffect(() => {
    fetchNearbyStoreData(locationCordinate)
      .then((res) => {
        res.features = res.features.filter((item) => {
          if(item.properties.name) return item
        })
        setNearByStore(res.features)
      })
      .catch((err) => {
        console.log(err)
      })
  }, [locationCordinate]);

  useEffect(() => {
    getLocationFromCoordinates(locationCordinate)
      .then((res) => {
          setAddress(res.results)
          console.log(address)
      })
      .catch((err)=>{
        console.log(err)
      })
  }, [locationCordinate]);

  return (
    <Paper elevation={3} rounded style={{ marginBottom: '30px' }}>
      <CardHeader
        title={thisRequest.fullName}
        subheader={thisRequest.jobTitle + " | " + thisRequest.medicalFacility}
        className={classes.reqres}
        classes={{
          title: classes.title,
          subheader: classes.subheader
        }}
      />
      <CardContent>
        <p><strong>LOCATION</strong></p>
        <p>{thisRequest.location}</p>
        <p><strong>EMAIL</strong></p>
        <p>{thisRequest.email}</p>
        <p><strong>NEEDS</strong></p>
        <p>
        {thisRequest.work && thisRequest.work.map((wor) => {
          return wor + ", "
        })}
        </p>
        <p><strong>DETAILS</strong></p>
        <p>{thisRequest.describeRequest}</p>
        <p><strong>RECOMMENED NEARBY SHOP (under 2km)</strong></p>

        {nearByStore && <CustomizedTables datas={nearByStore}/>}

        <Button onClick={loadStore}>Load Stores</Button>
      </CardContent>
      <Button
        style={{
          backgroundColor: '#F65058FF',
          color: 'white',
          fontWeight: 'bold',
          fontFamily: 'Lexend Giga'
        }}
        onClick={handlerOfferHelp}
      >Offer Help</Button>
    </Paper>
  )
}

export default RequestResponse;