import React, { useContext } from "react";
import localeContext, { getText } from "../../context/localeCtx";
import { useHistory } from "react-router-dom";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { Button, MenuItem, FormControl, Select } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import authContext from '../../context/auth';

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "row",
    justifyContent: 'space-between',
    marginTop: 5,
    marginBottom: 10,
  },
  buttonGroup: {
    display: "flex",
    justifyContent: "flex-end",
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  font: {
    fontFamily: "Merriweather",
    fontSize: "15px",
  },
  fontLogo: {
    fontFamily: "Yeseva One",
  },
  formControl: {
    marginTop: "15px",
  },
  btn_volunteer: {
    padding: 10,
    fontFamily: "Faustina",
    fontSize: "20px",
    color: "#3f51b5",
    "&:hover": {
      cursor: "pointer",
      textDecoration: "underline",
    },
  },
  btn_request_help: {
    padding: 10,
    fontFamily: "Faustina",
    fontSize: "20px",
    color: "#c51162",
    "&:hover": {
      cursor: "pointer",
      textDecoration: "underline",
    },
  },
  btn_nearby_stores: {
    padding: 10,
    fontFamily: "Faustina",
    fontSize: "20px",
    color: "#008000",
    "&:hover": {
      cursor: "pointer",
      textDecoration: "underline",
    },
  },
}));
const Header = () => {
  const classes = useStyles();
  const history = useHistory();
  const locale = useContext(localeContext);
  const handleRouteSwitch = (path) => {
    history.push("/" + path);
  };

  const signOut = () =>{
    console.log(authUser.token)
    localStorage.removeItem("jwt")  
    history.push("/" + "auth")
  }

  const { authUser } = useContext(authContext);

  return (
    <div className={classes.root}>
      <Button
        onClick={() => {
          handleRouteSwitch("");
        }}
        className={classes.avatar}
        style={{ backgroundColor: "white" }}
      >
        {/* <FavoriteIcon style={{ color: "red" }} /> */}
        <span
          style={{ marginLeft: 5, fontSize: "30px" }}
          className={classes.fontLogo}
        >
          DoctorAlly
        </span>
      </Button>
      <div className={classes.buttonGroup}>
      <Button
          variant="outlined"
          onClick={() => { handleRouteSwitch("covid19_data") }}
          style={{ backgroundColor: '#ff6600', color: 'white' }}
          color="primary"
          className={classes.btn_volunteer}
        >
          {getText("header", "covid19_data", locale.lang)}
      </Button>
        {/* <strong
          onClick={() => {
            handleRouteSwitch("volunteer");
          }}
          color="primary"
          className={classes.btn_volunteer}
        >
          {getText("header", "volunteer", locale.lang)}
        </strong>
        <strong
          onClick={() => {
            handleRouteSwitch("request_help/1");
          }}
          color="secondary"
          className={classes.btn_request_help}
        >
          {getText("header", "request_help", locale.lang)}
        </strong>
        <strong
          onClick={() => {
            handleRouteSwitch("supply_stores");
          }}
          className={classes.btn_nearby_stores}
        >
          {getText("header", "supply_stores", locale.lang)}
        </strong> */}
        
        {authUser && authUser.user.role === "volunteer" ? (
        <>
          <Button
          variant="outlined"
          onClick={() => { handleRouteSwitch("volunteer") }}
          style={{ backgroundColor: '#357CCD', color: 'white' }}
          color="primary"
          className={classes.btn_volunteer}
        >
          {getText("header", "volunteer", locale.lang)}
          </Button>
        <Button
          variant="outlined"
          onClick={() => { handleRouteSwitch("supply_stores") }}
          className={classes.btn_nearby_stores}
          style={{ backgroundColor: 'green', color: 'white' }}>
        {getText("header", "supply_stores", locale.lang)}</Button>
        </>
        )
        :(
        <>
        <Button
          variant="outlined"
          onClick={() => { handleRouteSwitch("request_help/1") }}
          style={{ backgroundColor: 'red', color: 'white' }}
          color="secondary"
          className={classes.btn_request_help}
        >
          {getText("header", "request_help", locale.lang)}
          </Button>
          <Button
            onClick={() => {
            handleRouteSwitch("supply_stores");
          }}
          className={classes.btn_nearby_stores}
          style={{ backgroundColor: 'green', color: 'white' }}
          >
        {getText("header", "supply_stores", locale.lang)}</Button>
        </>
        )}
      
        {!authUser ? 
        (<Button
          variant="outlined"
          onClick={() => { handleRouteSwitch("auth") }}
          className={classes.btn_nearby_stores}
          // style={{ backgroundColor: 'green', color: 'white' }}
        >Login/Register</Button>):(
          <>
          <Button
          variant="outlined"
          // onClick={() => { handleRouteSwitch("profile") }}
          className={classes.btn_nearby_stores}
          // style={{ backgroundColor: 'green', color: 'white' }}
        >{authUser.user.username}'s as {authUser.user.role}</Button>

          <Button
          variant="outlined"
          onClick={() => signOut()}
          className={classes.btn_nearby_stores}
          // style={{ backgroundColor: 'green', color: 'white' }}
        >Sign Out</Button>
          </>
        )}

        {/* <FormControl className={classes.formControl}>
          <Select
            value={locale.lang}
            onChange={(e) => {
              locale.setLang(e.target.value);
            }}
            className={classes.font}
          >
            <MenuItem value={"ENG"} className={classes.font}>
              ENG
            </MenuItem>
            <MenuItem value={"VIE"} className={classes.font}>
              VIE
            </MenuItem>
          </Select>
        </FormControl> */}
        {/* <FacebookConnect /> */}
      </div>
    </div>
  );
};
export default Header;